/**=====================
     Dark CSS Start
==========================**/
body {
    &.dark-only {
        background-color: $dark-body-background;
        color: $dark-all-font-color;
        .navbar {
            background-color: $dark-card-background;
            border-bottom: 1px solid $dark-card-border;
            .collapse {
                border-right: 1px solid $dark-card-border;
            }
        }
        .nav-link {
            color: $dark-all-font-color;
        }

        h1, h2, h3, h4, h5, h6 {
            color: $dark-all-font-color;
        }
        .parent-title, .main-title {
            color: $dark-all-font-color;
        }

        .menu {
            > .nav-item {
                > .nav-link {
                    color: $dark-all-font-color;
                }
                &.active {
                    > .nav-link {
                        color: $primary-color;
                    }
                }
            }
        }

        .direct {
            .nav-link {
                background-color: $dark-card-background;
                color: $dark-all-font-color;
            }
        }

        .nav-sidebar {
            > .nav-item {
                .title {
                    background-color: $dark-card-background;
                    color: $dark-all-font-color;
                }
            }
        }

        .card {
            background-color: $dark-card-background;
        }

        .main-table-sec {
            .table-wrapper {
                .table-scroll {
                    table {
                        tr {
                            &:first-child {
                                border-bottom: 1px solid $dark-card-border;
                                span {
                                    color: $dark-all-font-color;
                                }
                            }
                        }
                    }
                }
            }
        }
        .page-footer {
            .page-changes-summary {
                border-top: 1px solid $dark-card-border;
            }
        }
    }
}
/**=====================
     Dark CSS End
==========================**/